import { AppShell, Burger, Group, UnstyledButton, useMantineColorScheme } from '@mantine/core';
import { useDisclosure } from '@mantine/hooks';
import * as classes from './Layout.module.css';
import { IconMoon, IconSun } from '@tabler/icons-react';

import React from 'react';
import { Link } from 'gatsby';

export const Layout: React.FC<React.PropsWithChildren> = ({ children }) => {
  const [opened, { toggle }] = useDisclosure();
  const { colorScheme, toggleColorScheme } = useMantineColorScheme();

  return (
    <AppShell header={{ height: 60 }} navbar={{ width: 300, breakpoint: 'sm', collapsed: { desktop: true, mobile: !opened } }} padding='md'>
      <AppShell.Header>
        <Group h='100%' px='md'>
          <Burger opened={opened} onClick={toggle} hiddenFrom='sm' size='sm' />
          <Group justify='space-between' style={{ flex: 1 }}>
            West Coast Racing Standings Tracker
            <Group ml='xl' gap={0} visibleFrom='sm'>
              <UnstyledButton component={Link} to='/' className={classes.control}>
                Home
              </UnstyledButton>
              <UnstyledButton component={Link} to='/danger-rating' className={classes.control}>
                Danger Rating
              </UnstyledButton>
              {/* <UnstyledButton component={Link} to='/cars' className={classes.control}>
                Cars
              </UnstyledButton> */}
              <UnstyledButton className={classes.control} onClick={toggleColorScheme}>
                {colorScheme === 'light' ? <IconMoon className={classes.icon} /> : <IconSun className={classes.icon} />}
              </UnstyledButton>
            </Group>
          </Group>
        </Group>
      </AppShell.Header>

      <AppShell.Navbar py='md' px={4}>
        <UnstyledButton component={Link} to='/' className={classes.control}>
          Home
        </UnstyledButton>
        <UnstyledButton component={Link} to='/danger-rating' className={classes.control}>
          Danger Rating
        </UnstyledButton>
        {/* <UnstyledButton component={Link} to='/cars' className={classes.control}>
          Cars
        </UnstyledButton> */}
      </AppShell.Navbar>

      <AppShell.Main>{children}</AppShell.Main>
    </AppShell>
  );
};
